.personal {
	display: flex;
	flex-direction: column;
}

.personal__photo {
	align-self: center;
	border-radius: 50%;
	margin-top: 30px;
}

.personal__fieldset {
	display: flex;
	flex-direction: column;
	margin-bottom: 33px;
	position: relative;

	&::after {
		background-image: url("../images/approved.png");
		background-repeat: no-repeat;
		bottom: -22px;
		content: "";
		display: none;
		height: 166px;
		position: absolute;
		right: -16px;
		width: 184px;
		z-index: 1;
	}
}

.personal__input-label-pair {
	position: relative;
	width: 100%;
}

.personal__input-text {
	@include roboto(500, 12px, normal);
	background-color: transparent;
	border: 2px solid $color-lighter-black;
	border-width: 0 0 2px 0;
	box-sizing: border-box;
	color: $color-darker-gray;
	height: 32px;
	letter-spacing: .1em;
	margin-top: 23px;
	text-transform: uppercase;
	width: 100%;

	&:first-of-type {
		margin-top: 20px;
	}
}

.personal__input-text[placeholder] {
	color: $color-darker-gray;
	opacity: 1;
}

.personal__input-text::-webkit-input-placeholder {
	color: $color-darker-gray;
	opacity: 1;
}

.personal__input-text::-moz-placeholder {
	color: $color-darker-gray;
	opacity: 1;
}

.personal__input-text:-moz-placeholder {
	color: $color-darker-gray;
	opacity: 1;
}

.personal__input-text:-ms-input-placeholder {
	color: $color-darker-gray;
	opacity: 1;
}

.personal__input-text:invalid {
	border: 2px solid $color-lighter-black;
	border-width: 0 0 2px 0;
	box-shadow: none;
	outline: none;
}

.personal__label {
	bottom: -20px;
	color: $color-dark-gray;
	display: none;
	left: 0;
	position: absolute;
	font-size: 8px;
}

.personal__input-text:focus + .personal__label,
.personal__input-text:valid + .personal__label {
	display: block;
}

@media (min-width: 400px) {
	.personal__fieldset::after {
		display: block;
	}
}

@media (min-width: 663px) {
	.personal {
		display: flex;
		flex-direction: row;
	}

	.personal__photo {
		align-self: flex-start;
	}

	.personal__fieldset {
		margin-left: 21px;
	}

	.personal__input-label-pair {
		width: 460px;
	}
}


