.js-level {
	align-items: center;
	display: flex;
	flex-direction: column;
	min-height: 250px;
}

.js-level__gauge {
	height: 158px;
	margin-top: 42px;
	width: 316px;
}

.js-level__heading-value {
	margin-top: 45px;
	text-align: center;
}

.js-level__heading {
	color: $color-light-black;
	font-size: 18px;
	letter-spacing: 0;
	text-transform: none;
	margin-bottom: 12px;
}

.js-level__value {
	@include roboto(900, 50px, 60px);
	color: $color-blue;
	margin: 0;
}

@media (min-width: 663px) {
	.js-level {
		align-items: flex-start;
		flex-direction: row;
	}

	.js-level__gauge {
		margin-left: -31px;
	}

	.js-level__heading-value {
		margin-left: 36px;
	}

	.js-level__heading {
		width: 200px;
	}

	.js-level__heading-value {
		text-align: left;
	}
}
