.skills {
	padding-top: 1px;
}

.skills__fieldset {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
	padding: 2px 0 15px 0;
}

.skills__heading {
	color: $color-light-black;
	font-size: 18px;
	letter-spacing: 0;
	text-transform: none;
	margin-bottom: 0;
	margin-top: 30px;
}

.skills__input-label-pair {
	margin: 18.5px 0;
}

.skills__input-checkbox {
	display: none;
}

.skills__label {
	padding-left: 27px;
	position: relative;

}

.skills__input-checkbox + .skills__label:before {
	background-color: $color-gray;
	border-radius: 4px;
	content: "";
	cursor: pointer;
	height: 19px;
	left: 0;
	position: absolute;
	top: 0;
	width: 19px;
}

.skills__input-checkbox:checked + .skills__label:before {
	background-color: $color-blue;
	background-image: url("../images/check.svg");
	background-position: center center;
	background-repeat: no-repeat;
	content:"";
}

@media (min-width: 400px) {
	.skills__fieldset {
		height: 394px;
	}

	.skills__input-label-pair {
		width: 50%;
	}
}

@media (min-width: 663px) {
	.skills__fieldset {
		height: 197px;
	}

	.skills__input-label-pair {
		width: 25.8%;
	}
}

