html {
  font-family: "Roboto", Tahoma, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 22px;
  box-sizing: border-box;
  color: #666666;
  letter-spacing: .1em;
  text-transform: uppercase; }

.page {
  background-color: #f0f0f0;
  min-width: 320px; }

.main-content {
  padding-bottom: 100px;
  padding-top: 60px; }

.main-content__form {
  width: 100%; }

.main-content__form-section {
  background-color: #fefdfb;
  padding: 0 21px; }
  .main-content__form-section--personal {
    background-image: url("../images/backgrounds/personal_bg.jpg"); }
  .main-content__form-section--skills {
    background-image: url("../images/backgrounds/skills_bg.jpg"); }
  .main-content__form-section--js-level {
    background-image: url("../images/backgrounds/js-level_bg.jpg"); }

.main-content__form-section:not(:last-of-type) {
  margin-bottom: 2px; }

.main-content__form-section-fieldset {
  margin: 0;
  padding: 0;
  border: none;
  outline: none; }

@media (min-width: 663px) {
  .main-content__form {
    margin: 0 auto;
    width: 662px; } }

.personal {
  display: flex;
  flex-direction: column; }

.personal__photo {
  align-self: center;
  border-radius: 50%;
  margin-top: 30px; }

.personal__fieldset {
  display: flex;
  flex-direction: column;
  margin-bottom: 33px;
  position: relative; }
  .personal__fieldset::after {
    background-image: url("../images/approved.png");
    background-repeat: no-repeat;
    bottom: -22px;
    content: "";
    display: none;
    height: 166px;
    position: absolute;
    right: -16px;
    width: 184px;
    z-index: 1; }

.personal__input-label-pair {
  position: relative;
  width: 100%; }

.personal__input-text {
  font-family: "Roboto", Tahoma, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: normal;
  background-color: transparent;
  border: 2px solid #333;
  border-width: 0 0 2px 0;
  box-sizing: border-box;
  color: #666666;
  height: 32px;
  letter-spacing: .1em;
  margin-top: 23px;
  text-transform: uppercase;
  width: 100%; }
  .personal__input-text:first-of-type {
    margin-top: 20px; }

.personal__input-text[placeholder] {
  color: #666666;
  opacity: 1; }

.personal__input-text::-webkit-input-placeholder {
  color: #666666;
  opacity: 1; }

.personal__input-text::-moz-placeholder {
  color: #666666;
  opacity: 1; }

.personal__input-text:-moz-placeholder {
  color: #666666;
  opacity: 1; }

.personal__input-text:-ms-input-placeholder {
  color: #666666;
  opacity: 1; }

.personal__input-text:invalid {
  border: 2px solid #333;
  border-width: 0 0 2px 0;
  box-shadow: none;
  outline: none; }

.personal__label {
  bottom: -20px;
  color: #cccccc;
  display: none;
  left: 0;
  position: absolute;
  font-size: 8px; }

.personal__input-text:focus + .personal__label,
.personal__input-text:valid + .personal__label {
  display: block; }

@media (min-width: 400px) {
  .personal__fieldset::after {
    display: block; } }

@media (min-width: 663px) {
  .personal {
    display: flex;
    flex-direction: row; }
  .personal__photo {
    align-self: flex-start; }
  .personal__fieldset {
    margin-left: 21px; }
  .personal__input-label-pair {
    width: 460px; } }

.skills {
  padding-top: 1px; }

.skills__fieldset {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 2px 0 15px 0; }

.skills__heading {
  color: #010101;
  font-size: 18px;
  letter-spacing: 0;
  text-transform: none;
  margin-bottom: 0;
  margin-top: 30px; }

.skills__input-label-pair {
  margin: 18.5px 0; }

.skills__input-checkbox {
  display: none; }

.skills__label {
  padding-left: 27px;
  position: relative; }

.skills__input-checkbox + .skills__label:before {
  background-color: #eeeeee;
  border-radius: 4px;
  content: "";
  cursor: pointer;
  height: 19px;
  left: 0;
  position: absolute;
  top: 0;
  width: 19px; }

.skills__input-checkbox:checked + .skills__label:before {
  background-color: #0093d7;
  background-image: url("../images/check.svg");
  background-position: center center;
  background-repeat: no-repeat;
  content: ""; }

@media (min-width: 400px) {
  .skills__fieldset {
    height: 394px; }
  .skills__input-label-pair {
    width: 50%; } }

@media (min-width: 663px) {
  .skills__fieldset {
    height: 197px; }
  .skills__input-label-pair {
    width: 25.8%; } }

.js-level {
  align-items: center;
  display: flex;
  flex-direction: column;
  min-height: 250px; }

.js-level__gauge {
  height: 158px;
  margin-top: 42px;
  width: 316px; }

.js-level__heading-value {
  margin-top: 45px;
  text-align: center; }

.js-level__heading {
  color: #010101;
  font-size: 18px;
  letter-spacing: 0;
  text-transform: none;
  margin-bottom: 12px; }

.js-level__value {
  font-family: "Roboto", Tahoma, sans-serif;
  font-size: 50px;
  font-weight: 900;
  line-height: 60px;
  color: #0093d7;
  margin: 0; }

@media (min-width: 663px) {
  .js-level {
    align-items: flex-start;
    flex-direction: row; }
  .js-level__gauge {
    margin-left: -31px; }
  .js-level__heading-value {
    margin-left: 36px; }
  .js-level__heading {
    width: 200px; }
  .js-level__heading-value {
    text-align: left; } }
